import axios from 'axios'
import { wait } from './utils.js'

//
//  _____
// |_   _|   _ _ __   ___  ___
//   | || | | | '_ \ / _ \/ __|
//   | || |_| | |_) |  __/\__ \
//   |_| \__, | .__/ \___||___/
//       |___/|_|
// https://patorjk.com/software/taag/#p=display&c=c&f=Standard&t=Types
//

/**
 * @typedef  {object} Tender
 * @property {string} description   - The tender's label as it should be displayed on the terminal.
 * @property {string} feeAmount     - The fee amount for this tender.
 * @property {string} primaryAmount - The primary amount for this tender.
 * @property {string} totalAmount   - The total amount (primary + fee) for this tender.
 */

/**
 * @typedef  {object} TenderSummary
 * @property {string} feeAmount     - The total fee amount for all items in the cart.
 * @property {string} primaryAmount - The total primary amount for all items in the cart.
 * @property {string} totalAmount   - The total amount (primary + fee) for all items in the cart.
 * @property {Tender[]} tenders     - A list of tenders for this transaction.
 */

/**
 * @typedef  {object} Item
 * @property {string} description   - The item's label as it should be displayed on the terminal.
 * @property {string} primaryAmount - The primary amount for the item.
 */

/**
 * @typedef  {object} ItemSummary
 * @property {string} primaryAmount - The total primary amount for all items in the cart.
 * @property {Item[]} items         - An array of items in the cart.
 */

/**
 * @typedef  {object} ActivationRequest
 * @property {string} tenderType      - The tender type of this card. Either credit_card  or debit_card.
 * @property {string} vaultToken      - The DataVault token under which this card's data will be stored.
 * @property {string} vaultUrl        - The base DataVault URL that GWA will use to store data. This determines what
 * environment GWA is running in.
 * @property {string} [feeAmount]     - The fee amount being charged to this card.
 * @property {string} [primaryAmount] - The primary amount being charged to this card.
 * @property {string} [totalAmount]   - The total amount (primary + fee) being charged to this card.
 */

// I'd prefer this enum were declared inside the class but jsdoccing enums can
// be really sticky and this is the only way to get it right
/**
 * @typedef {ACTIVATION_STATUSES} ActivationStatus One of GWA's enum values
 */

/**
 *
 * Enum representing the statuses that GWA promises to return. ***(TODO: GWA to determine enum values.)***
 * @readonly
 * @enum {string}
 */
let ACTIVATION_STATUSES = {
  /**
    * Indicated the data is stored in DataVault and processing may continue. (This will be the `activationStatus` of the
    * `ReaderStatusResponse` which is provided as the final argument to an `onStatus` callback, as well as the
    * resolution value of a successful `readCardData`.)
    */
  STORED: 'data_stored',
}
ACTIVATION_STATUSES = Object.freeze(ACTIVATION_STATUSES)

/**
 * @typedef  {object} ReaderStatusResponse
 * @property {ActivationStatus} activationStatus - The current status of the activation.
 * @property {string} tenderType                 - The tender type of the activation. Either credit_card  or debit_card.
 * @property {string} vaultToken                 - The DataVault token under which data will be or was stored.
 * @property {object} [hints]                    - A key/value pairing of DataVault hints for the card that was stored.
 * This will only be returned if the activation_status is data_stored.
 */

/**
 * Pulled from https://github.com/axios/axios#response-schema
 * @typedef  {object} AxiosResponse
 * @property {object} data       - The response that was provided by the server.
 * @property {string} status     - The HTTP status code from the server response.
 * @property {string} statusText - The HTTP status message from the server response.
 * @property {object} headers    - The HTTP headers that the server responded with. All header names are lower cased and
 * can be accessed using the bracket notation. Example: `response.headers['content-type']`.
 * @property {object} config     - The config that was provided to `axios` for the request.
 * @property {object} request    - The request that generated this response. It is the last ClientRequest instance in
 * node.js (in redirects) and an XMLHttpRequest instance in the browser.
 */

//
//  ____ _______        __
// |  _ \_   _\ \      / /
// | |_) || |  \ \ /\ / /
// |  __/ | |   \ V  V /
// |_|    |_|    \_/\_/
//

// This *must* be defined top level due to a jsdoc bug

/**
 * Convenience class for indicating that an error is handleable.
 *
 * If a standard GSG API error format is available then PaymentTerminalWidget will throw an instance of this class
 * where displayMessage is the error message and context is the full GSG API error.
 *
 * https://support.grantstreet.com/wiki/display/GUILD/Communicating+API+Errors
 */
class OperationalError extends Error {
  // E.g.
  // {
  //   errorCode: 'InvalidRoutingNumber',
  //   errorMessage: 'routing number has an invalid checksum',
  //   displayMessage: 'This routing number does not appear to be valid.',
  // }
  operational = true

  /**
   * @constructor
   * @param  {string} message - The error message. (Should be a GSG API error format displayMessage.)
   * @param  {object} context - The full API error.
   */
  constructor (message, context) {
    super(message)
    this.context = context
  }
}

/**
 * A library to simplify communication with the GWA Payment Terminal Module.
 *
 * Requests can be made manually, via helpers like getReaderStatus, or via convenience methods like readCardData.
 *
 * Some communication with the end user can be deferred to PTW by the parent application by passing arguments like
 * showLoader. If this is done then some methods may display loading indicators, or in the event of a failure which
 * results in a GSG Standard API Error containing a displayMessage, error messages.
 */
class PaymentTerminalWidget {

  /**
   * Exposes the OperationalError class
   */
  static OperationalError = OperationalError

  static #ACTIVATION_STATUSES = ACTIVATION_STATUSES

  // This is exposed for easy mocking
  axios

  #baseUrl
  #pollingInterval
  #readerPollingTimeout
  // A callback set by the PTW consumer which will be called on conclusion of
  // a successful reader polling process
  #statusCallback
  // Internal flag used to stop polling
  #watchStatus
  // Injected convenience functions
  #showMessage
  #hideMessage
  #showLoader
  #hideLoader

  /**
   * @constructor
   * @param  {object} [params]                                      - A params object.
   * @param  {string} [params.gwaEndpoint=http://localhost:47252]   - The GWA API to call.
   * @param  {number} [params.pollingInterval=1000]                 - The minimum time between calls generated by the
   * readCardData end function.
   * @param  {number} [params.readerPollingTimeout]                 - The maximum time spend polling the cardReader for
   * a 'data_stored' status. If PTW doesn't receive a 'data_stored' status before readerPollingTimeout is reached then
   * polling will be aborted and an error will be thrown.
   * @param  {number} [params.axiosTimeout=60 * 1000]               - Axios' internal request timeout setting.
   * @param  {function} [params.showMessage]                        - A convenience function enabling PTW to show a
   * message in the parent application.
   * @param  {function} [params.hideMessage]                        - A convenience function enabling PTW to hide a
   * previously shown message.
   * @param  {function} [params.showLoader]                         - A convenience function enabling PTW to show a
   * loading indicator in the parent application.
   * @param  {function} [params.hideLoader]                         - A convenience function enabling PTW to hide a
   * previously shown loading indicator.
   */
  constructor ({
    gwaEndpoint = 'http://localhost:47252',
    pollingInterval = 1000,
    readerPollingTimeout,
    // 60 seconds
    axiosTimeout = 60 * 1000,
    showMessage,
    hideMessage,
    showLoader,
    hideLoader,
  } = {}) {
    this.#baseUrl = gwaEndpoint
    this.#pollingInterval = pollingInterval
    this.#readerPollingTimeout = readerPollingTimeout

    this.#showMessage = showMessage
    this.#hideMessage = hideMessage
    this.#showLoader = showLoader
    this.#hideLoader = hideLoader

    this.axios = axios.create({
      timeout: axiosTimeout,
    })

    this.axios.interceptors.response.use(
      async response => response,
      async error => {
        let message = `API request error: `

        if (error.response) {
          // The request was made and the server responded with a status code
          // that falls out of the range of 2xx

          // If this is a standard GSG API error response throw a handleable
          // error
          const data = error.response.data || {}
          if (data.displayMessage) {
            throw new PaymentTerminalWidget.OperationalError(data.displayMessage, data)
          }

          // Otherwise just throw
          message += `Server returned ${error.response.status} error`
          try {
            message += ` with data: ${JSON.stringify(data)}`
          } catch (error) {
            // There was a problem adding json context. Skip it.
          }
        } else if (error.request) {
          // The request was made but no response was received
          message += `No server response. Requested: ${JSON.stringify(error.request._options?.pathname)}`
        } else {
          // Something happened in setting up the request that triggered an
          // Error
          message += `Error setting up request. Error: ${JSON.stringify(error)}`
        }

        throw new Error(message)
      },
    )
  }

  /**
   * Makes an http GET to this.#baseUrl + the provided path.
   * @param  {string} path            - The path to be requested.
   * @param  {object} options         - axios options object.
   * @throws {Error}                  - Throws if there was an error with the http request.
   * @throws {OperationalError}       - Throws if the request returned a standard GSG API error format.
   * @return {Promise<AxiosResponse>} - An axios response.
   */
  #get (path, options) {
    return this.axios.get(this.#url(path), options)
  }

  /**
   * Makes an http HEAD to this.#baseUrl + the provided path.
   * @param  {string} path            - The path to be requested.
   * @param  {object} options         - axios options object.
   * @throws {Error}                  - Throws if there was an error with the http request.
   * @throws {OperationalError}       - Throws if the request returned a standard GSG API error format.
   * @return {Promise<AxiosResponse>} - An axios response.
   */
  #head (path, options) {
    return this.axios.head(this.#url(path), options)
  }

  /**
   * Makes an http POST to this.#baseUrl + the provided path.
   * @param  {string} path            - The path to be requested.
   * @param  {object} data            - POST data.
   * @param  {object} options         - axios options object.
   * @throws {Error}                  - Throws if there was an error with the http request.
   * @throws {OperationalError}       - Throws if the request returned a standard GSG API error format.
   * @return {Promise<AxiosResponse>} - An axios response.
   */
  #post (path, data, options) {
    return this.axios.post(this.#url(path), data, options)
  }

  /**
   * Makes an http PATCH to this.#baseUrl + the provided path.
   * @param  {string} path            - The path to be requested.
   * @param  {object} data            - PATCH data.
   * @param  {object} options         - axios options object.
   * @throws {Error}                  - Throws if there was an error with the http request.
   * @throws {OperationalError}       - Throws if the request returned a standard GSG API error format.
   * @return {Promise<AxiosResponse>} - An axios response.
   */
  #patch (path, data, options) {
    return this.axios.patch(this.#url(path), data, options)
  }

  /**
   * Makes an http PUT to this.#baseUrl + the provided path.
   * @param  {string} path            - The path to be requested.
   * @param  {object} data            - PUT data.
   * @param  {object} options         - axios options object.
   * @throws {Error}                  - Throws if there was an error with the http request.
   * @throws {OperationalError}       - Throws if the request returned a standard GSG API error format.
   * @return {Promise<AxiosResponse>} - An axios response.
   */
  #put (path, data, options) {
    return this.axios.put(this.#url(path), data, options)
  }

  /**
   * Makes an http DELETE to this.#baseUrl + the provided path.
   * @param  {string} path            - The path to be requested.
   * @param  {object} options         - axios options object.
   * @throws {Error}                  - Throws if there was an error with the http request.
   * @throws {OperationalError}       - Throws if the request returned a standard GSG API error format.
   * @return {Promise<AxiosResponse>} - An axios response.
   */
  #delete (path, options) {
    return this.axios.delete(this.#url(path), options)
  }

  /**
   * Returns either the passed path the path concatenated with this.#baseUrl
   * @param  {string} path - The path to be requested.
   * @return {string}      - The url.
   */
  #url (path) {
    if (!this.#baseUrl) {
      return path
    }
    const slash = path.match(/^\//) ? '' : '/'
    return this.#baseUrl + slash + path
  }

  /**
   * Utility function which throws if the required http status is not present.
   * @async
   * @param  {Promise<AxiosResponse>|AxiosResponse} response - Axios response object (or promise).
   * @param  {number} response.status                        - Http status code returned from a request.
   * @param  {number} code                                   - HTTP status code to be required.
   * @throws {Error}                                         - Throws if the required status code was not returned.
   * @throws {OperationalError}                              - Throws if the request returned a standard GSG API error
   * format.
   * @return {Promise<AxiosResponse>}                        - An axios response.
   */
  async #requireCode (response, code) {
    const { status } = await response
    if (status !== code) {
      throw new Error(`API request error: Server returned unacceptable code. ${status} !== ${code}.`)
    }
    return response
  }

  /**
   * Prompts GWA to display the default home screen.
   *
   * If the terminal is not yet opened and claimed, will do so first.
   * @async
   * @throws {Error}              - Throws if request fails to return 204.
   * @throws {OperationalError}   - Throws if the request returned a standard GSG API error format.
   * @return {Promise<undefined>} - Returns undefined.
   */
  displayHome () {
    return this.#requireCode(this.#post('/displayHome'), 204)
  }

  /**
   * Prompts GWA to display a message on the terminal's display. This should be used when the message is not meant to
   * end a transaction.
   *
   * E.G., May be used to display a "PROCESSING..." message while the tenders are being authorized or to display a
   * decline message prior to collecting another tender from the customer.
   *
   * If the terminal is not yet opened and claimed, will do so first.
   * @async
   * @param  {string} message     - The message to be displayed.
   * @throws {Error}              - Throws if request fails to return 204.
   * @throws {OperationalError}   - Throws if the request returned a standard GSG API error format.
   * @return {Promise<undefined>} - Returns undefined.
   */
  displayMessage (message) {
    return this.#requireCode(this.#post('/displayHome', { data: { message } }), 204)
  }

  /**
   * Prompts GWA to turn the display off and discard the most recent card details.
   * @async
   * @throws {Error}              - Throws if request fails to return 204.
   * @throws {OperationalError}   - Throws if the request returned a standard GSG API error format.
   * @return {Promise<undefined>} - Returns undefined.
   */
  displayOff () {
    return this.#requireCode(this.#post('/displayOff'), 204)
  }

  /**
   * Prompts GWA to display "APPROVED" on the terminal's display for 15 seconds before turning the display off. This
   * request also tells GWA to discard the last card details.
   *
   * If the terminal is not yet opened and claimed, will do so first.
   * @async
   * @throws {Error}              - Throws if request fails to return 204.
   * @throws {OperationalError}   - Throws if the request returned a standard GSG API error format.
   * @return {Promise<undefined>} - Returns undefined.
   */
  displayApproval () {
    return this.#requireCode(this.#post('/displayApproval'), 204)
  }

  /**
   * Prompts GWA to display the summary screen, listing all tenders and the total amount to be paid by the customer.
   *
   * If the terminal is not yet opened and claimed, will do so first.
   * @async
   * @param  {TenderSummary} tenderSummary - A summary of the charges and tenders to display.
   * @throws {Error}                       - Throws if request fails to return 204.
   * @throws {OperationalError}            - Throws if the request returned a standard GSG API error format.
   * @return {Promise<undefined>}          - Returns undefined.
   */
  displayTenders (tenderSummary) {
    return this.#requireCode(this.#post('/displayTenders', { data: tenderSummary }), 204)
  }

  /**
   * Prompts GWA to display the listed items.
   *
   * If the terminal is not yet opened and claimed, will do so first.
   * @async
   * @param  {ItemSummary} itemSummary - A summary of items to be charged.
   * @throws {Error}                   - Throws if request fails to return 204.
   * @throws {OperationalError}        - Throws if the request returned a standard GSG API error format.
   * @return {Promise<undefined>}      - Returns undefined.
   */
  displayItems (itemSummary) {
    return this.#requireCode(this.#post('/displayItems', { data: itemSummary }), 204)
  }

  /**
   * Prompts GWA to activate the terminal's card reader.
   *
   * After data is collected, GWA may display a message like "Data collected, please wait...". If the customer cancels
   * the activation on the terminal, GWA may display a message like "Canceled, please wait...".
   *
   * If the terminal is not yet opened and claimed, will do so first.
   * @async
   * @param  {ActivationRequest} activationRequest - A summary of the charge.
   * @throws {Error}                               - Throws if request fails to return 204.
   * @throws {OperationalError}                    - Throws if the request returned a standard GSG API error format.
   * @return {Promise<undefined>}                  - Returns undefined.
   */
  activateReader (activationRequest) {
    return this.#requireCode(this.#post('/activateReader', { data: activationRequest }), 204)
  }

  /**
   * Gets details from GWA about the current or most recent activation.
   *
   * A response contains the current state of the activation and card hints, if data has been stored for this
   * activation. Errors getting the current status (like if the terminal is not currently active) will use the standard
   * GSG error format.
   * @async
   * @param  {string} vaultToken             - The DV token under which data will be stored.
   * @throws {Error}                         - Throws if request fails to return 200.
   * @throws {OperationalError}              - Throws if the request returned a standard GSG API error format.
   * @return {Promise<ReaderStatusResponse>} - Returns the status of the reader.
   */
  getReaderStatus (vaultToken) {
    // TODO Add verbose logging
    return this.#requireCode(this.#get('/readerStatus', { params: { vaultToken } }), 200)
  }

  /**
   * @callback onStatus
   * @param {ReaderStatusResponse} status - The status of the reader.
   */

  /**
   * Set a callback to be called whenever a readCardData receives a status from reader polling. *Does not get called
   * after getReaderStatus.*
   * @param  {onStatus} callback - The callback.
   */
  onStatus (callback) {
    this.#statusCallback = callback
  }

  /**
   * Deep clones a status returned from the /readerStatus endpoint.
   *
   * This is not schema specific but the JSON method used here isn't safe for Date objects, undefined, Infinity,
   * circular structures, etc.
   *
   * If at any point a different method is required then de/restructuring syntax is a likely candidate.
   * @param  {ReaderStatusResponse} status - The status to clone.
   * @return {ReaderStatusResponse}        - The clone of the passed status.
   */
  #cloneStatus (status) {
    return JSON.parse(JSON.stringify(status))
  }

  /**
   * Cancels polling process started by readCardData. (This will cause the promise returned by readCardData to resolve
   * *after any current polling request has resolved.*)
   */
  stopPollingStatus () {
    this.#watchStatus = false
  }

  /**
   * Prompts GWA to cancel a currently active terminal activation.
   *
   * The terminal display may display a message like "Canceled, please wait..." until the next API request is received.
   *
   * Cancels polling if active.
   * @async
   * @throws {Error}              - Throws if request fails to return 204.
   * @throws {OperationalError}   - Throws if the request returned a standard GSG API error format.
   * @return {Promise<undefined>} - Returns the status of the reader.
   */
  cancelReaderActivation () {
    if (this.#watchStatus) {
      this.stopPollingStatus()
    }
    return this.#requireCode(this.#post('/deactivateReader'), 204)
  }

  /**
   * Shows a message and hides any loaders if the error is operational and those functionalities were provided to PTW.
   * @param  {Error} error - The error to be handled or thrown.
   * @throws {Error}       - Throws if the error is not operational or if no handlers were provided.
   */
  #handleError (error) {
    this.#hideLoader?.()
    if (this.#showMessage && error.operational) {
      this.#showMessage(error.message)
      return
    }
    throw error
  }

  /**
   * Convenience function which activates the card reader, sets up reader status polling (with callbacks), and resolves
   * when polling is complete.
   *
   * This can be canceled with cancelReaderActivation or polling can be stopped with stopPollingStatus.
   *
   * @param  {object} params                               - Parameters Object.
   * @param  {ActivationRequest} params.activationRequest  - A summary of the charge.
   * @param  {onStatus} [params.callback]                  - A callback to be called on each status update.
   * @param  {boolean} [params.populateInputsAfterStorage] - If true this.populateInputsFromHints will be called after
   * storage is complete, before promise resolution.
   * @param  {object} [params.DataVault]                   - A DV instance. Required if populateInputsFromHints is true.
   * @param  {object} [params.fieldMap]                    - An object mapping standard dv field names to those used by
   * the consuming application.
   * @throws {Error}                                       - Throws if the reader is already being watched.
   * @throws {Error}                                       - Throws if request fails to return 200.
   * @throws {OperationalError}                            - Throws if the request returned a standard GSG API error
   * format.
   * @throws {TypeError}                                   - Throws if activationRequest doesn't contain a vaultToken.
   * @throws {Error}                                       - Throws if this.#readerPollingTimeout is reached before a
   * 'data_stored' status is received.
   * @return {Promise<ActivationStatus|undefined>}         - Returns a promise which resolve to the status of the
   * reader, or undefined if reader observation was canceled.
   */
  async readCardData ({
    activationRequest,
    onStatus: callback,
    populateInputsAfterStorage = false,
    DataVault,
    fieldMap,
  } = {}) {
    // Lock
    if (this.#watchStatus) {
      throw new Error('Already watching reader status')
    }

    if (!activationRequest?.vaultToken) {
      throw new TypeError('vaultToken is required')
    }

    this.#showLoader?.()

    this.#watchStatus = true

    // Activate reader
    try {
      await this.activateReader(activationRequest)
    } catch (error) {
      this.stopPollingStatus()
      this.#handleError(error)
      // Quit if no error was thrown
      return
    }

    // Set callback
    if (callback) {
      this.onStatus(callback)
    }

    const startTime = Date.now()

    // eslint-disable-next-line no-unmodified-loop-condition
    while (
      // While polling is not canceled
      this.#watchStatus && (
        // And it hasn't timed out (assuming there's a timeout set)
        !this.#readerPollingTimeout ||
        Date.now() < (startTime + this.#readerPollingTimeout)
      )
    ) {
      let status
      // Wait minimum time
      const waitPromise = wait(this.#pollingInterval)

      try {
        ({ data: status } = await this.getReaderStatus(activationRequest.vaultToken))
      } catch (error) {
        this.stopPollingStatus()
        this.#handleError(error)
        // Quit if no error was thrown
        return
      }

      // If the watch was canceled while the request was in flight, then quit
      if (!this.#watchStatus) {
        this.#hideLoader?.()
        return
      }

      if (typeof this.#statusCallback === 'function') {
      // Don't let consumer mutate status
        this.#statusCallback(this.#cloneStatus(status))
      }

      if (status.activationStatus === PaymentTerminalWidget.#ACTIVATION_STATUSES.STORED) {
        this.stopPollingStatus()

        if (populateInputsAfterStorage) {
          this.populateInputsFromHints({
            fieldsAndHints: status.hints,
            DataVault,
            fieldMap,
          })
        }

        this.#hideLoader?.()
        return status
      }

      // Wait minimum time before iterating
      await waitPromise
    }

    // Handles for when polling is canceled between requests
    if (!this.#watchStatus) {
      this.#hideLoader?.()
      return
    }

    this.stopPollingStatus()
    throw new Error(`Reader status polling request timed out after ${this.#readerPollingTimeout}`)
  }

  /**
   * Populates inputs configured with passed DV instance using passed hintData.
   * @param  {object} params.fieldsAndHints                - An object with DV hints keyed by type.
   * @param  {object} params.DataVault                     - A DV instance.
   * @param  {object} [params.fieldMap]                   - An object mapping standard dv field names to those used by
   * the consuming application.
   * @param  {object} [params.clearDataVaultOverlaps=true] - This isn't directly related to input management, but is a
   * convenient safety measure. It asks DV to clear the DB of any depreciated stored data which might be left over in
   * the event of a tender change and multiple storage requests.
   * @throws {Error}                                       - Throws if a DataVault instance is not passed.
   */
  populateInputsFromHints ({
    fieldsAndHints,
    DataVault,
    fieldMap,
    clearDataVaultOverlaps = true,
  } = {}) {

    // fieldsAndHints should have a structure similar to:
    // {
    //   // Where the top level key is the DataVault BE uses to store a
    //   // particular
    //   field. ("Field" in this case not meaning "input".)
    //   card_swipe: {
    //       // And nested keys being information about that field which can
    //       // safely be exposed
    //       card_swipe_cardholder_name:                  "GOOD+VISA+TEST",
    //       card_swipe_credit_card_number_brand:         "VISA",
    //   },
    //   pin_block: {
    //       pin_block: 1
    //   },
    //   ksn: {
    //       key_serial_number: 1
    //   }
    // }

    // fieldMap should have a structure similar to:
    // {
    //   card_swipe:  "cc_card_swipe",
    //   contactless: "cc_contactless",
    //   ksn:         "cc_ksn",
    //   pin_block:   "cc_encrypted_pin"
    // };

    if (!DataVault) {
      throw new Error('No DataVault API provided.')
    }

    for (const [storageName, hints] of Object.entries(fieldsAndHints)) {
      // Get fieldId from map if the consumer uses different DV field names
      const fieldId = fieldMap?.[storageName] || storageName

      // Clear this field and any overlapping input names
      const fieldSpec = DataVault.userFieldMap?.[fieldId]
      if (!fieldSpec) {
        console.log(`${fieldId} is not configured in DataVault`)
        continue
      }

      if (clearDataVaultOverlaps) {
        DataVault.clear(fieldSpec.overlaps || [], { clear: 'storage' })
      }

      // TODO: DV shouldn't need the storage name because it's already getting
      // the appropriate subset of hints. Maybe make it not need that?
      DataVault.populateInputsFromHints(fieldId, hints, storageName)

      const successCallback = DataVault.callbacks?.success
      if (successCallback) {
        successCallback({
          field: fieldSpec,
          hints,
          status: 'success',
        })
      }
    }
  }

}

// Keep this on a separate line so jsdoc doesn't bug out
export default PaymentTerminalWidget
